<template>
	<view class="content">
		<view class="goods-section margin-top-sm padding-top-sm" v-for="(item, index) in list" :key="index" @click="selectGoods(index)">
			<view class="g-item">
				<image :src="item.img"></image>
				<view class="right">
					<text class="title clamp">{{item.name}}</text>
					<text v-if="item.good_sku_id" class="spec">{{item.specification}}</text>
					<view class="price-box">
						<text class="price text-red text-price">{{item.price}}</text>
					</view>
				</view>
				<view class="checkbox">
					<checkbox class='round red' :class="item.checked?'checked':''" :checked="item.checked?true:false" :value="item.index"></checkbox>
				</view>
			</view>
		</view>
		<button class="add-btn" @click="confirm">提交</button>
	</view>
</template>

<script>
	import Measurement from '../../api/measurement'
	import { objectURLToBlob } from '../../utils'
	export default {
		data() {
			return {
				list: [],
				setData: [],
				oldList: []
			}
		},
		onLoad(option){
			this.oldList = option.good ? option.good.split(',') : []
			this.oid = option.oid
			this.getList()
		},
		methods: {
			async getList(){
				let title = '分配商品'
				const that = this
				uni.setNavigationBarTitle({
					title
				})
				await Measurement.getCommodityList({
					id: this.oid
				},function(res){
					for(var k in res){
						if(that.oldList.length>0){
							if(that.oldList.indexOf(res[k].id.toString()) !== -1){
								res[k].checked = true
							}
						}else{
							
							res[k].checked = false
						}
						
						if(res[k].good_sku){
							res[k].good_sku.product_sku.forEach(item=>{
								if(res[k].specification){
									res[k].specification+= item.value + ';'
								}else{
									res[k].specification = item.value + ';'
								}
								
							})
							res[k].specification = res[k].specification.substr(0,res[k].specification.length-1)
						}
					}
					that.list = res
				})
			},
			
			//选择商品
			selectGoods(index){
				this.$set(this.list[index], 'checked', !this.list[index].checked);
				
			},
			// 提交
			confirm(){
				this.$api.prePage().refreshGoodsAllocated(this.list);
				uni.navigateBack()
			}
		}
	}
</script>

<style lang="scss">
	page{
		background: $page-color-base;
		padding-top: 16upx;
	}
	.goods-section {
		background: #fff;
		padding-bottom: 1px;
	
		.g-header {
			display: flex;
			align-items: center;
			height: 84upx;
			padding: 0 30upx;
			position: relative;
		}
	
		.logo {
			display: block;
			width: 50upx;
			height: 50upx;
			border-radius: 100px;
		}
	
		.name {
			font-size: 30upx;
			color: $font-color-base;
			margin-left: 24upx;
		}
	
		.g-item {
			display: flex;
			margin: 20upx 30upx;
			image {
				flex-shrink: 0;
				display: block;
				width: 140upx;
				height: 140upx;
				border-radius: 4upx;
			}
	
			.right {
				flex: 6;
				padding-left: 24upx;
				overflow: hidden;
			}
			.checkbox{
				flex: 1;
				padding-left: 24upx;
				overflow: hidden;
			}
			.title {
				font-size: 30upx;
				color: $font-color-dark;
			}
	
			.spec {
				font-size: 26upx;
				color: $font-color-light;
			}
	
			.price-box {
				display: flex;
				align-items: center;
				font-size: 32upx;
				color: $font-color-dark;
				padding-top: 10upx;
	
				.price {
					margin-bottom: 4upx;
				}
				.number{
					font-size: 26upx;
					color: $font-color-base;
					margin-left: 20upx;
				}
				.delete-button{
					position:absolute;
					right:20upx;
				}
			}
			.text-icon{
				font-size: 40upx;
			}
			.step-box {
				position: relative;
			}
		}
	}
	.add-btn{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 690upx;
		height: 80upx;
		margin: 60upx auto;
		font-size: $font-lg;
		color: #fff;
		background-color: $base-color;
		border-radius: 10upx;
		box-shadow: 1px 2px 5px rgba(219, 63, 96, 0.4);
	}
</style>
